
.js-chess-components {
    position: relative;
    height: 100%;
}

.js-chess-visible-components {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
    z-index: 0;
}

.js-chess-header {
    height: 50px;
    min-height: 50px;
    width: 100%;
    background-image: linear-gradient(saddlebrown, #ffcd66);
    padding-top: 10px;
    padding-bottom: 10px;
    margin-top: 0px;
    box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.5);
    position: relative;
}

.js-chess-body {
    width: 100%;
}

.js-restart-button {
    height: 50%;
    aspect-ratio: 2 / 1;
    background-color: AliceBlue;
    font-family: 'Inter', sans-serif;
    font-size: 25px;
    border: none;
    box-shadow: 5px 5px 10px 2px rgba(0, 0, 0, 0.5);
    border-radius: 15%;
    transition: color 0.3s ease-in-out;
    position: absolute;
    top: 50%;
    right: 0;
    margin-right: 20px;
    transform: translateY(-50%);
    
}

.js-chess-title {
    font-family: 'Inter', sans-serif;
    font-size: 50px;
    font-weight: bold;
    margin: 0;
    padding-left: 20px;
    padding-right: 20px;
}

.js-input-result {
    height: 100px;
    width: 800px;
    display: flex;
    justify-content: center;
    align-items: center;
    /*background-color: #222222;*/
}

.js-input-result-text {
    font-size: 30px;
    font-family: 'Inter', sans-serif;
}

.js-promotion-popup {
    display: flex;
    flex-wrap: wrap;
    background-image: linear-gradient(saddlebrown, #ffcd66);
    border-radius: 20vh / 20vh;
}

.js-horizontal-wrap {
    width: 100%;
    display: flex;
    justify-content: center;
}

.js-popup {
    width: 100%;
    height: 100vh;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 3;
}

.js-promotion-image {
    height: 100px;
    width: 100px;
    padding: 35px;
}