.js-chessboard {
    height: 800px;
    width: 800px;
    padding-top: 35px;
    display: flex;
    flex-wrap: wrap;
}

.js-chessbox {
    height: 100px;
    width: 100px;
    position: relative;
}
.js-chessbackgroundboxlight {
    background-color: #ffcd66;
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: 1;
}
.js-chessbackgroundboxdark {
    background-color: saddlebrown;
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: 1;
}
.js-chesspiece {
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: 2;
}
.js-chesspiece-selected {
    background-color: rgba(0, 0, 0, 0.5);
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: 2;
}
.js-pieceimage {
    height: 100%;
    width: 100%;
}
